import type {Timing} from "@swim/mapping";
import {AnyLength, Length} from "@swim/math";
import {Look, MoodVector, ThemeMatrix} from "@swim/theme";
import {
  ViewContextType,
  View,
  ViewAnimator,
  ViewFastener,
  PositionGestureInput,
  PositionGestureDelegate,
} from "@swim/view";
import {HtmlView, HtmlViewController} from "@swim/dom";
import {Graphics, HtmlIconView} from "@swim/graphics";
import {ButtonMembrane} from "@swim/controls";
import type {MagnifierHandleObserver} from "./MagnifierHandleObserver";

export class MagnifierHandle extends ButtonMembrane implements PositionGestureDelegate {
  constructor(node: HTMLElement) {
    super(node);
    this.onClick = this.onClick.bind(this);
    this.initHandle();
  }

  protected initHandle(): void {
    this.addClass("magnifier-handle");
    this.position.setAutoState("relative");
    this.flexShrink.setAutoState(0);
    this.marginTop.setAutoState(8);
    this.marginRight.setAutoState(8);
    this.marginBottom.setAutoState(4);
    this.marginLeft.setAutoState(8);
    this.overflowX.setAutoState("hidden");
    this.overflowY.setAutoState("hidden");
    this.pointerEvents.setAutoState("auto");
    this.userSelect.setAutoState("none");
  }

  declare readonly viewController: HtmlViewController & MagnifierHandleObserver | null;

  declare readonly viewObservers: ReadonlyArray<MagnifierHandleObserver>;

  @ViewAnimator({type: Length, inherit: true})
  declare collapsedWidth: ViewAnimator<this, Length | undefined, AnyLength | undefined>;

  @ViewAnimator({type: Number, inherit: true, updateFlags: View.NeedsAnimate})
  declare drawerStretch: ViewAnimator<this, number | undefined>; // 0 = collapsed; 1 = expanded

  @ViewFastener<MagnifierHandle, HtmlView, Graphics>({
    key: true,
    type: HtmlView,
    onSetView(iconView: HtmlView | null): void {
      if (iconView !== null) {
        this.owner.initIcon(iconView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createIcon();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      parentView.appendChildView(childView, key);
    },
    fromAny(value: HtmlView | Graphics): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else {
        const iconView = this.owner.createIcon();
        iconView.graphics.setAutoState(value);
        return iconView;
      }
    },
  })
  declare icon: ViewFastener<MagnifierHandle, HtmlView, Graphics>;

  protected createIcon(): HtmlIconView {
    return HtmlIconView.create();
  }

  protected initIcon(iconView: HtmlView): void {
    iconView.position.setAutoState("absolute");
    iconView.top.setAutoState(0);
    iconView.right.setAutoState(0);
    iconView.width.setAutoState(this.height.state);
    iconView.height.setAutoState(this.height.state);
    if (iconView instanceof HtmlIconView) {
      iconView.iconWidth.setAutoState(24);
      iconView.iconHeight.setAutoState(24);
      iconView.iconColor.setState(this.getLookOr(Look.backgroundColor, null));
    }
  }

  @ViewFastener<MagnifierHandle, HtmlView, string | undefined>({
    key: true,
    type: HtmlView,
    onSetView(labelView: HtmlView | null): void {
      if (labelView !== null) {
        this.owner.initLabel(labelView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createLabel();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      targetView = this.owner.icon.view;
      parentView.insertChildView(childView, targetView, key);
    },
    fromAny(value: HtmlView | string | undefined): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else if (typeof value === "string") {
        const labelView = this.owner.createLabel();
        labelView.text(value);
        return labelView;
      } else {
        return null;
      }
    },
  })
  declare label: ViewFastener<MagnifierHandle, HtmlView, string | undefined>;

  protected createLabel(): HtmlView {
    const labelView = HtmlView.span.create();
    labelView.display.setAutoState("block");
    labelView.fontFamily.setAutoState("system-ui, 'Open Sans', sans-serif");
    labelView.fontSize.setAutoState(17);
    labelView.whiteSpace.setAutoState("nowrap");
    labelView.textOverflow.setAutoState("ellipsis");
    labelView.overflowX.setAutoState("hidden");
    labelView.overflowY.setAutoState("hidden");
    return labelView;
  }

  protected initLabel(labelView: HtmlView): void {
    labelView.position.setAutoState("absolute");
    labelView.top.setAutoState(0);
    labelView.left.setAutoState(this.height.state);
    labelView.right.setAutoState(this.height.state);
    labelView.textAlign.setAutoState("center");
    if (labelView.color.isAuto()) {
      labelView.color.setAutoState(this.getLookOr(Look.backgroundColor, null));
    }
  }

  @ViewFastener<MagnifierHandle, HtmlView, Graphics>({
    key: true,
    type: HtmlView,
    onSetView(accessoryView: HtmlView | null): void {
      if (accessoryView !== null) {
        this.owner.initAccessory(accessoryView);
      }
    },
    createView(): HtmlView | null {
      return this.owner.createAccessory();
    },
    insertView(parentView: View, childView: HtmlView, targetView: View | null, key: string | undefined): void {
      targetView = this.owner.label.view;
      if (targetView === null) {
        targetView = this.owner.icon.view;
      }
      parentView.insertChildView(childView, targetView, key);
    },
    fromAny(value: HtmlView | Graphics): HtmlView | null {
      if (value instanceof HtmlView) {
        return value;
      } else {
        const accessoryView = this.owner.createAccessory();
        accessoryView.graphics.setAutoState(value);
        return accessoryView;
      }
    },
  })
  declare accessory: ViewFastener<MagnifierHandle, HtmlView, Graphics>;

  protected createAccessory(): HtmlIconView {
    return HtmlIconView.create();
  }

  protected initAccessory(accessoryView: HtmlView): void {
    accessoryView.position.setAutoState("absolute");
    accessoryView.top.setAutoState(0);
    accessoryView.left.setAutoState(0);
    accessoryView.width.setAutoState(this.height.state);
    accessoryView.height.setAutoState(this.height.state);
    if (accessoryView instanceof HtmlIconView) {
      accessoryView.iconWidth.setAutoState(24);
      accessoryView.iconHeight.setAutoState(24);
      accessoryView.iconColor.setState(this.getLookOr(Look.backgroundColor, null));
    }
  }

  protected onApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean): void {
    super.onApplyTheme(theme, mood, timing);
    const itemColor = theme.getOr(Look.backgroundColor, mood, null);

    if (this.backgroundColor.isAuto()) {
      this.backgroundColor.setAutoState(theme.getOr(Look.accentColor, mood, null), timing);
    }

    const iconView = this.icon.view;
    if (iconView instanceof HtmlIconView) {
      iconView.iconColor.setState(itemColor, timing);
    }

    const labelView = this.label.view;
    if (labelView !== null) {
      labelView.color.setAutoState(itemColor, timing);
    }

    const accessoryView = this.accessory.view;
    if (accessoryView instanceof HtmlIconView) {
      accessoryView.iconColor.setState(itemColor, timing);
    }
  }

  protected onMount(): void {
    super.onMount();
    this.on("click", this.onClick);
  }

  protected onUnmount(): void {
    this.off("click", this.onClick);
    super.onUnmount();
  }

  protected onAnimate(viewContext: ViewContextType<this>): void {
    super.onAnimate(viewContext);
    this.lineHeight.setAutoState(this.height.state);
    const drawerStretch = this.drawerStretch.value;
    if (drawerStretch !== void 0) {
      const labelView = this.label.view;
      if (labelView !== null) {
        labelView.display.setAutoState(drawerStretch === 0 ? "none" : "block");
        labelView.opacity.setAutoState(drawerStretch);
      }
      const accessoryView = this.accessory.view;
      if (accessoryView !== null) {
        accessoryView.display.setAutoState(drawerStretch === 0 ? "none" : "block");
        accessoryView.opacity.setAutoState(drawerStretch);
      }
    }
  }

  protected onLayout(viewContext: ViewContextType<this>): void {
    super.onLayout(viewContext);

    if (viewContext.viewIdiom === "mobile") {
      this.height.setAutoState(48);
    } else {
      this.height.setAutoState(36);
    }
    this.lineHeight.setAutoState(this.height.state);

    const iconView = this.icon.view;
    if (iconView !== null) {
      iconView.width.setAutoState(this.height.state);
      iconView.height.setAutoState(this.height.state);
    }

    const labelView = this.label.view;
    if (labelView !== null) {
      labelView.left.setAutoState(this.height.state);
      labelView.right.setAutoState(this.height.state);
    }

    const accessoryView = this.accessory.view;
    if (accessoryView !== null) {
      accessoryView.width.setAutoState(this.height.state);
      accessoryView.height.setAutoState(this.height.state);
    }
  }

  protected glow(input: PositionGestureInput): void {
    const drawerStretch = this.drawerStretch.getStateOr(0);
    if (drawerStretch === 0) {
      super.glow(input);
    }
  }

  protected onClick(event: MouseEvent): void {
    event.stopPropagation();

    const viewObservers = this.viewObservers;
    for (let i = 0, n = viewObservers.length; i < n; i += 1) {
      const viewObserver = viewObservers[i]!;
      if (viewObserver.magnifierHandleDidPress !== void 0) {
        viewObserver.magnifierHandleDidPress(this);
      }
    }
    const viewController = this.viewController;
    if (viewController !== null && viewController.magnifierHandleDidPress !== void 0) {
      viewController.magnifierHandleDidPress(this);
    }
  }
}
