import type {Timing} from "@swim/mapping";
import {Look, Feel, MoodVector, ThemeMatrix} from "@swim/theme";
import type {HtmlView} from "@swim/dom";
import {WidgetView} from "./WidgetView";

export class WidgetCard extends WidgetView {
  constructor(node: HTMLElement) {
    super(node);
    this.initWidget();
    this.initTheme();
  }

  protected initWidget(): void {
    this.addClass("widget");
    this.position.setAutoState("relative");
    this.borderTopRightRadius.setAutoState(4);
    this.borderBottomRightRadius.setAutoState(4);
    this.borderBottomLeftRadius.setAutoState(4);
    this.borderTopLeftRadius.setAutoState(4);
    this.boxSizing.setAutoState("border-box");
    this.overflowX.setAutoState("hidden");
    this.overflowY.setAutoState("hidden");
    this.pointerEvents.setAutoState("auto");
  }

  protected initTheme(): void {
    this.modifyTheme(Feel.default, [Feel.raised, 1]);
  }

  protected initHeader(headerView: HtmlView): void {
    super.initHeader(headerView);
    headerView.display.setAutoState("flex");
    headerView.justifyContent.setAutoState("space-between");
    headerView.height.setAutoState(36);
    headerView.paddingLeft.setAutoState(18);
    headerView.paddingRight.setAutoState(18);
  }

  protected initHeaderTitle(titleView: HtmlView): void {
    super.initHeaderTitle(titleView);
    titleView.alignSelf.setAutoState("center");
    if (titleView.color.isAuto()) {
      titleView.color.setAutoState(this.getLookOr(Look.neutralColor, null));
    }
  }

  protected initHeaderSubtitle(subtitleView: HtmlView): void {
    super.initHeaderSubtitle(subtitleView);
    subtitleView.alignSelf.setAutoState("center");
    if (subtitleView.color.isAuto()) {
      subtitleView.color.setAutoState(this.getLookOr(Look.neutralColor, null));
    }
  }

  protected initFooter(footerView: HtmlView): void {
    super.initFooter(footerView);
    footerView.display.setAutoState("flex");
    footerView.justifyContent.setAutoState("space-between");
    footerView.height.setAutoState(12);
    footerView.paddingLeft.setAutoState(18);
    footerView.paddingRight.setAutoState(18);
  }

  protected onApplyTheme(theme: ThemeMatrix, mood: MoodVector, timing: Timing | boolean): void {
    super.onApplyTheme(theme, mood, timing);

    if (this.backgroundColor.isAuto()) {
      let backgroundColor = theme.getOr(Look.backgroundColor, mood, null);
      if (backgroundColor !== null) {
        backgroundColor = backgroundColor.alpha(0.9);
      }
      this.backgroundColor.setAutoState(backgroundColor, timing);
    }

    const titleView = this.headerTitle.view;
    if (titleView !== null) {
      if (titleView.color.isAuto()) {
        titleView.color.setAutoState(this.getLookOr(Look.neutralColor, null));
      }
    }

    const subtitleView = this.headerSubtitle.view;
    if (subtitleView !== null) {
      if (subtitleView.color.isAuto()) {
        subtitleView.color.setAutoState(this.getLookOr(Look.neutralColor, null));
      }
    }
  }
}
