import {Easing} from "@swim/mapping";
import type {GeoShape} from "@swim/geo";
import {ModelContext, Model, Trait} from "@swim/model";
import type {Color} from "@swim/style";
import {Look, Feel} from "@swim/theme";
import type {GestureInput} from "@swim/view";
import type {Graphics} from "@swim/graphics";
import {
  ComponentModel,
  ComponentTrait,
  ComponentView,
  ComponentFastener,
  CompositeComponent,
} from "@swim/component";
import {
  MapGraphicsViewContext,
  MapGraphicsView,
  MapCircleView,
  MapIconView,
  MapAreaView,
} from "@swim/map";
import {Status, StatusVector, StatusTrait, EntityTrait, EntityGroup} from "@swim/lens";
import type {Geographic} from "./geographic/Geographic";
import {GeographicView} from "./geographic/GeographicView";
import {LocationTrait} from "./location/LocationTrait";
import {DistrictTrait} from "./district/DistrictTrait";
import {AtlasEntityDistrict} from "./AtlasEntityDistrict";

export class AtlasEntityLocation extends CompositeComponent {
  protected initLayer(layerView: MapGraphicsView): void {
    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      const entityDistrict = this.subdistrict.injectComponent();
      if (entityDistrict !== null) {
        entityDistrict.layer.setView(layerView);
      }
    }
    this.updateLevelOfDetail(layerView.viewContext);
  }

  protected projectLayer(viewContext: MapGraphicsViewContext): void {
    this.updateLevelOfDetail(viewContext);
  }

  protected initGeographic(geographicView: GeographicView): void {
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      if (entityTrait.isSelected()) {
        geographicView.highlight();
      } else {
        geographicView.unhighlight();
      }
    }
    const statusTrait = this.status.trait;
    if (statusTrait !== null) {
      this.onSetStatus(statusTrait.statusVector, StatusVector.empty());
    }
    this.updateLevelOfDetail(geographicView.viewContext);
  }

  protected initEntity(entityTrait: EntityTrait): void {
    const geographicView = this.geographic.view;
    if (geographicView !== null) {
      if (entityTrait.isSelected()) {
        geographicView.highlight();
      } else {
        geographicView.unhighlight();
      }
      this.updateLevelOfDetail(geographicView.viewContext);
    }

    this.status.setTrait(entityTrait.getTrait(StatusTrait));
    this.location.setTrait(entityTrait.getTrait(LocationTrait));
    this.district.setTrait(entityTrait.getTrait(DistrictTrait));
  }

  protected initStatus(statusTrait: StatusTrait): void {
    this.onSetStatus(statusTrait.statusVector, StatusVector.empty());
  }

  protected initLocation(locationTrait: LocationTrait): void {
    this.onSetGeographic(locationTrait.geographic);
  }

  protected initDistrict(districtTrait: DistrictTrait): void {
    this.subentities.setModel(districtTrait.subdistricts.model);
  }

  protected onSetGeographic(geographic: Geographic | null): void {
    if (geographic !== null) {
      let geographicView = this.geographic.view;
      if (geographicView === null) {
        geographicView = GeographicView.fromGeographic(geographic);
        geographicView.modifyTheme(Feel.default, [Feel.primary, 1]);
        this.geographic.setView(geographicView);
      } else {
        geographicView.setState(geographic, Easing.linear.withDuration(5000));
      }
      const layerView = this.layer.view;
      if (layerView !== null) {
        this.geographic.injectView(layerView);
        this.updateLevelOfDetail(layerView.viewContext);
      }
    } else {
      this.geographic.setView(null);
    }
  }

  protected validateEntity(entityTrait: EntityTrait): void {
    const layerView = this.layer.view;
    if (layerView !== null) {
      this.updateLevelOfDetail(layerView.viewContext);
    }
  }

  protected attachSubentities(entityGroup: EntityGroup): void {
    const entityDistrict = this.subdistrict.injectComponent();
    if (entityDistrict !== null) {
      entityDistrict.entities.setModel(entityGroup);
    }
  }

  protected detachSubentities(entityGroup: EntityGroup): void {
    // hook
  }

  protected onSetTitle(title: string | undefined): void {
    // hook
  }

  protected onSetIcon(icon: Graphics | null): void {
    // hook
  }

  protected onSetStatus(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
    const geographicView = this.geographic.view;
    if (geographicView !== null) {
      const alert = newStatusVector.get(Status.alert) || 0;
      const warning = newStatusVector.get(Status.warning) || 0;
      const normal = newStatusVector.get(Status.normal);
      const inactive = newStatusVector.get(Status.inactive) || 0;
      if (alert !== 0) {
        geographicView.modifyMood(Feel.default, [Feel.warning, 1], [Feel.alert, alert]);
        if (!oldStatusVector.isEmpty()) {
          this.ripple(geographicView.getLook(Look.accentColor)!, 2, 5000);
        }
      } else if (warning !== 0) {
        geographicView.modifyMood(Feel.default, [Feel.warning, warning], [Feel.alert, void 0]);
        if (!oldStatusVector.isEmpty()) {
          this.ripple(geographicView.getLook(Look.accentColor)!, 1, 2500);
        }
      } else {
        geographicView.modifyMood(Feel.default, [Feel.warning, void 0], [Feel.alert, void 0]);
        if (normal !== void 0 && !oldStatusVector.isEmpty()) {
          this.ripple(geographicView.getLook(Look.accentColor)!, 1, 2500);
        }
      }
      if (inactive !== 0) {
        geographicView.modifyMood(Feel.default, [Feel.inactive, inactive]);
      } else {
        geographicView.modifyMood(Feel.default, [Feel.inactive, void 0]);
      }
    }
  }

  ripple(color: Color, width: number, duration: number): void {
    const geographicView = this.geographic.view;
    if (geographicView !== null && geographicView.isMounted() && geographicView.isPowered() &&
        !geographicView.isCulled() && geographicView.geoBounds.intersects(geographicView.viewContext.geoFrame)) {
      const geoCenter = geographicView instanceof MapCircleView ? geographicView.geoCenter.value
                      : geographicView instanceof MapIconView ? geographicView.geoCenter.value
                      : geographicView instanceof MapAreaView ? geographicView.geoBounds.center
                      : null;
      if (geoCenter !== null) {
        const ripple = new MapCircleView()
          .geoCenter(geoCenter)
          .radius(0)
          .stroke(color)
          .strokeWidth(width);
        const rootMapView = this.layer.view!.parentView! as MapGraphicsView;
        rootMapView.appendChildView(ripple);
        const frame = rootMapView.viewFrame;
        const radius = Math.min(frame.width, frame.height) / 8;
        const timing = Easing.linear.withDuration(duration);
        ripple.stroke(color.alpha(0), timing)
              .radius(radius, timing);
        ripple.radius.onEnd = function (): void {
          ripple.remove();
        };
      }
    }
  }

  protected onSelect(entityTrait: EntityTrait): void {
    const geographicView = this.geographic.view;
    if (geographicView !== null) {
      geographicView.highlight();
    }
  }

  protected onUnselect(entityTrait: EntityTrait): void {
    const geographicView = this.geographic.view;
    if (geographicView !== null) {
      geographicView.unhighlight();
    }
  }

  protected updateLevelOfDetail(viewContext: MapGraphicsViewContext): void {
    const locationTrait = this.location.trait;
    if (locationTrait !== null) {
      if (locationTrait.minZoom <= viewContext.mapZoom) {
        locationTrait.addTraitConsumer(this);
        const geographicView = this.geographic.view;
        if (geographicView !== null) {
          geographicView.setHidden(locationTrait.maxZoom < viewContext.mapZoom);
        }
      } else {
        locationTrait.removeTraitConsumer(this);
      }
    }

    const districtTrait = this.district.trait;
    if (districtTrait !== null) {
      let boundary = districtTrait.boundary;
      if (boundary === null) {
        const geographicView = this.geographic.view;
        if (geographicView !== null) {
          boundary = geographicView.geoBounds;
        }
      }
      if (districtTrait.minZoom <= viewContext.mapZoom &&
         (boundary === null || boundary.bounds.intersects(viewContext.geoFrame))) {
        this.startConsumingSubentities(districtTrait);
      } else {
        this.stopConsumingSubentities(districtTrait);
      }
    }
  }

  protected onPressGeographic(input: GestureInput, event: Event | null, geographicView: GeographicView): void {
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      if (!entityTrait.isSelected()) {
        entityTrait.select({multi: input.shiftKey});
      } else if (input.shiftKey) {
        entityTrait.unselect();
      } else {
        entityTrait.unselectAll();
      }
    }
  }

  protected onLongPressGeographic(input: GestureInput, geographicView: GeographicView): void {
    input.preventDefault();
    const entityTrait = this.entity.trait;
    if (entityTrait !== null) {
      if (!entityTrait.isSelected()) {
        entityTrait.select({multi: true});
      } else {
        entityTrait.unselect();
      }
    }
  }

  protected initSubdistrict(subdistrict: AtlasEntityDistrict): void {
    const layerView = this.layer.view;
    if (layerView !== null) {
      subdistrict.layer.setView(layerView);
      this.updateLevelOfDetail(layerView.viewContext);
    }
  }

  protected startConsumingSubentities(districtTrait: DistrictTrait): void {
    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      entityGroup.addModelConsumer(this);
    }
    districtTrait.addTraitConsumer(this);
  }

  protected stopConsumingSubentities(districtTrait: DistrictTrait): void {
    districtTrait.removeTraitConsumer(this);
    const entityGroup = this.subentities.model;
    if (entityGroup !== null) {
      entityGroup.removeModelConsumer(this);
    }
  }

  @ComponentView<AtlasEntityLocation, MapGraphicsView>({
    observe: true,
    onSetView(layerView: MapGraphicsView | null): void {
      if (layerView !== null) {
        this.owner.initLayer(layerView);
      }
    },
    viewDidProject(viewContext: MapGraphicsViewContext): void {
      this.owner.projectLayer(viewContext);
    },
  })
  declare layer: ComponentView<this, MapGraphicsView>;

  @ComponentView<AtlasEntityLocation, GeographicView>({
    observe: true,
    onSetView(geographicView: GeographicView | null): void {
      if (geographicView !== null) {
        this.owner.initGeographic(geographicView);
      }
    },
    geographicDidPress(input: GestureInput, event: Event | null, geographicView: GeographicView): void {
      this.owner.onPressGeographic(input, event, geographicView);
    },
    geographicDidLongPress(input: GestureInput, geographicView: GeographicView): void {
      this.owner.onLongPressGeographic(input, geographicView);
    },
  })
  declare geographic: ComponentView<this, GeographicView>;

  @ComponentFastener<AtlasEntityLocation, AtlasEntityDistrict>({
    type: AtlasEntityDistrict,
    onSetComponent(subdistrict: AtlasEntityDistrict | null): void {
      if (subdistrict !== null) {
        this.owner.initSubdistrict(subdistrict);
      }
    },
    createComponent(): AtlasEntityDistrict | null {
      return new AtlasEntityDistrict();
    },
  })
  declare subdistrict: ComponentFastener<this, AtlasEntityDistrict>;

  @ComponentTrait<AtlasEntityLocation, EntityTrait>({
    type: EntityTrait,
    observe: true,
    onSetTrait(entityTrait: EntityTrait | null): void {
      if (entityTrait !== null) {
        this.owner.initEntity(entityTrait);
      }
    },
    entityDidSetTitle(title: string | undefined): void {
      this.owner.onSetTitle(title);
    },
    entityDidSetIcon(icon: Graphics | null): void {
      this.owner.onSetIcon(icon);
    },
    entityDidSelect(entityTrait: EntityTrait): void {
      this.owner.onSelect(entityTrait);
    },
    entityWillUnselect(entityTrait: EntityTrait): void {
      this.owner.onUnselect(entityTrait);
    },
    traitDidValidate(modelContext: ModelContext, entityTrait: EntityTrait): void {
      this.owner.validateEntity(entityTrait);
    },
    traitDidInsertTrait(memberTrait: Trait, targetTrait: Trait | null): void {
      if (memberTrait instanceof StatusTrait) {
        this.owner.status.setTrait(memberTrait);
      } else if (memberTrait instanceof LocationTrait) {
        this.owner.location.setTrait(memberTrait);
      } else if (memberTrait instanceof DistrictTrait) {
        this.owner.district.setTrait(memberTrait);
      }
    },
  })
  declare entity: ComponentTrait<this, EntityTrait>;

  @ComponentTrait<AtlasEntityLocation, StatusTrait>({
    type: StatusTrait,
    observe: true,
    onSetTrait(statusTrait: StatusTrait | null): void {
      if (statusTrait !== null) {
        this.owner.initStatus(statusTrait);
      }
    },
    traitDidSetStatusVector(newStatusVector: StatusVector, oldStatusVector: StatusVector): void {
      this.owner.onSetStatus(newStatusVector, oldStatusVector);
    },
  })
  declare status: ComponentTrait<this, StatusTrait>;

  @ComponentTrait<AtlasEntityLocation, DistrictTrait>({
    type: DistrictTrait,
    observe: true,
    onSetTrait(districtTrait: DistrictTrait | null): void {
      if (districtTrait !== null) {
        this.owner.initDistrict(districtTrait);
      }
    },
    traitDidInsertChildModel(childModel: Model, targetModel: Model | null, districtTrait: DistrictTrait): void {
      if (childModel === districtTrait.subdistricts.model) {
        this.owner.subentities.setModel(childModel as EntityGroup);
      }
    },
    districtDidSetZoomRange(minZoom: number, maxZoom: number, districtTrait: DistrictTrait): void {
      districtTrait.requireUpdate(Model.NeedsValidate);
    },
    districtDidSetBoundary(newBoundary: GeoShape | null, oldBoundary: GeoShape | null, districtTrait: DistrictTrait): void {
      districtTrait.requireUpdate(Model.NeedsValidate);
    },
  })
  declare district: ComponentTrait<this, DistrictTrait>;

  @ComponentTrait<AtlasEntityLocation, LocationTrait>({
    type: LocationTrait,
    observe: true,
    onSetTrait(locationTrait: LocationTrait | null): void {
      if (locationTrait !== null) {
        this.owner.initLocation(locationTrait);
      }
    },
    locationDidSetZoomRange(minZoom: number, maxZoom: number, locationTrait: LocationTrait): void {
      locationTrait.requireUpdate(Model.NeedsValidate);
    },
    locationDidSetGeographic(geographic: Geographic | null): void {
      this.owner.onSetGeographic(geographic);
    },
    traitWillStartConsuming(): void {
      const geographicView = this.owner.geographic.view;
      if (geographicView !== null) {
        const layerView = this.owner.layer.view;
        if (layerView !== null) {
          this.owner.geographic.injectView(layerView);
        }
      }
    },
    traitDidStopConsuming(): void {
      const geographicView = this.owner.geographic.view;
      if (geographicView !== null) {
        geographicView.remove();
      }
    },
  })
  declare location: ComponentTrait<this, LocationTrait>;

  @ComponentModel<AtlasEntityLocation, EntityGroup>({
    type: EntityGroup,
    observe: true,
    onSetModel(newEntityGroup: EntityGroup | null, oldEntityGroup: EntityGroup | null): void {
      if (oldEntityGroup !== null) {
        this.owner.detachSubentities(oldEntityGroup);
      }
      if (newEntityGroup !== null) {
        this.owner.attachSubentities(newEntityGroup);
      }
    },
    modelDidStopConsuming(entityGroup: EntityGroup): void {
      entityGroup.removeAll();
    },
  })
  declare subentities: ComponentModel<this, EntityGroup>;

  protected onUnmount(): void {
    super.onUnmount();
    this.geographic.removeView();
  }
}
